import "./List.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Table, Button, Popconfirm } from "antd";

const List = ({ username, idToken, shortURL, setError }) => {
  const [urls, setUrls] = useState([]);
  const [refreshUrls, setRefreshUrls] = useState(false);
  const [count, setCount] = useState(1);
  const [selectedRows, setSelectedRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [pageNumber, SetPageNumber] = React.useState(1);

  const columns = [
    {
      title: "Short",
      dataIndex: "short",
      render: (short, { idx }) => (
        <a
          target="_blank"
          rel="noreferrer"
          onClick={() => {
            const temp = [...urls];
            temp[idx] = {
              ...temp[idx],
              clicks: temp[idx].clicks + 1,
            };
            setUrls(temp);
          }}
          href={window.location.href + short}
        >
          {short}
        </a>
      ),
    },
    {
      title: "Full",
      dataIndex: "full",
      render: (full) => <a href={full}>{full.slice(0, 50) + "..."}</a>,
    },
    {
      title: "Clicks",
      dataIndex: "clicks",
    },
    {
      title: "Protected",
      dataIndex: "password",
      render: (password) => (password ? <p>&#x1F512;</p> : <h3>-</h3>),
    },
  ];

  useEffect(() => {
    axios
      .post("/api", { idToken, username, pageNumber })
      .then((res) => {
        let finalList = [];
        res.data.shortUrls.forEach(
          ({ short, full, clicks, _id, password }, idx) => {
            finalList.push({
              short,
              full,
              clicks,
              key: _id,
              password: password ? true : false,
              idx,
            });
          }
        );
        setUrls(finalList);
        axios
          .post("/api/shortUrls/count", { idToken, username })
          .then(({ data: { count } }) => setCount(count))
          .catch((err) => {
            console.log(err);
            setError("Something went wrong");
          });
      })
      .catch((err) => {
        console.log(err);
        setError("Something went wrong");
      });
  }, [username, refreshUrls, idToken, setError, pageNumber]);

  useEffect(() => {
    setRefreshUrls((prev) => !prev);
  }, [shortURL]);

  const deleteUrlsFromList = () => {
    setLoading(true);
    const ids = [];
    selectedRows.forEach((key) => {
      ids.push(key);
    });
    axios
      .delete("/api/shortUrls", { data: { idToken, username, ids } })
      .then(() => {
        setSelectedRows([]);
        setRefreshUrls((prev) => !prev);
        setLoading(false);
        SetPageNumber(1);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
        setError("Something went wrong");
      });
  };

  const rowSelection = {
    selectedRows,
    onChange: (selectedRowKeys) => setSelectedRows(selectedRowKeys),
  };
  const hasSelected = selectedRows.length > 0;

  return (
    <div className="list-container">
      <p>Table of links generated</p>
      <div style={{ marginBottom: 16 }}>
        <Popconfirm
          title="Are you sure to delete selected URLs?"
          onCancel={deleteUrlsFromList}
          okText="Cancel"
          cancelText="Delete"
        >
          <Button type="danger" disabled={!hasSelected} loading={loading}>
            Delete
          </Button>
        </Popconfirm>
        <span style={{ marginLeft: 8 }}>
          {hasSelected ? `Selected ${selectedRows.length} items` : ""}
        </span>
      </div>
      <Table
        rowSelection={rowSelection}
        columns={columns}
        dataSource={urls}
        scroll={{ x: 1000 }}
        pagination={{
          total: count,
          showSizeChanger: false,
          onChange: (page) => SetPageNumber(page),
        }}
      />
    </div>
  );
};

export default List;
